import React, { useState } from 'react';
import Form from '@rjsf/core';
import validator from '@rjsf/validator-ajv8';
import * as Realm from "realm-web";
import config from '../../config';
import "./index.css"
import { type } from '@testing-library/user-event/dist/type';

const app = new Realm.App({ id: `${config.API}` });
const user = app.currentUser;
const schema = {
    type: "object",
    properties: {
        Input: {
            type: "object",
            properties: {
                Thng_tin_nhn_vin: {
                    type: "object",
                    properties: {
                        Name: {
                            type: "string"
                        },
                        ID: {
                            type: "number"
                        }
                    },
                    required: [
                        "Name",
                        "ID"
                    ]
                },
                Part: {
                    type: "object",
                    properties: {
                        Part: {
                            type: "string",
                            enum: [
                                "Teaching Department",
                                "Technical Department",
                                "HR Department",
                                "Production Department",
                                "Supervision Department",
                                "CEO",
                                "R&D",
                                "CFO"
                            ]
                        },
                        Lng_c_bn: {
                            type: "number",
                            title: "Lng c bn"
                        }
                    },
                    required: [
                        "Part",
                        "Lng_c_bn"
                    ],
                    title: "B phn"
                },
                Plan: {
                    type: "object",
                    properties: {
                        Ch_tiu: {
                            type: "array",
                            items: {
                                type: "object",
                                properties: {
                                    Tn_ch_tiu: {
                                        type: "string",
                                        title: "Tn ch tiu"
                                    },
                                    S_tin: {
                                        type: "number"
                                    }
                                },
                                required: [
                                    "Tn_ch_tiu",
                                    "S_tin"
                                ]
                            },
                            title: "Ch tiu"
                        }
                    },
                    required: [
                        "Ch_tiu"
                    ]
                },
                Budget_performance: {
                    type: "number",
                    title: "Balance",
                    description: "Nu t trn 55% plan th nh gi l complete, mc lng t 0-54% ng vi h s 0, mc lng t 55-70% tnh h s 0.6, mc lng t 71-90% tnh tng ng vi h s 0.8, mc lng t 91-100% tnh h s 1.1"
                },
                Bonus: {
                    type: "number",
                    title: "Tin thng",
                    description: "Nhp s tin thng."
                }
            },
            required: [
                "Thng_tin_nhn_vin",
                "Part",
                "Plan",
                "Budget_performance",
                "Bonus"
            ]
        },
        Datetime: {
            type: "string",
            format: "date-time"
        },
    },
    required: [
        "Input",
        "Datetime"
    ],
    title: "--Lng cha thu--"
};
const schema1 = {
    type: "object",
    properties: {
        Input: {
            type: "object",
            properties: {
                Datetime: {
                    type: "string",
                    format: "date-time"
                },
                Tn_nhn_vin: {
                    type: "string",
                    title: "H v tn"
                },
                ID: {
                    type: "number"
                },
                Lng_cha_thu: {
                    type: "number",
                    default: "0",
                    title: "Lng cha thu"
                },
                H_s: {
                    type: "string",
                    enum: [
                        "TN*5%",
                        "TN*10%-0.25 triu VND",
                        "TN*15%-0.75 triu VND",
                        "TN*20%-1.65 triu VND",
                        "TN*25%-3.25 triu VND",
                        "TN*30%-5.85 triu VND",
                        "TN*35%-9.85 triu VND"
                    ],
                    default: "0",
                    title: "Chn bc"
                },
                "Gim tr bn thn": {
                    type: "number",
                    default: "0"
                },
                Bo_him: {
                    type: "number",
                    title: "Tin ng bo him"
                }
            },
            title: "Input"
        },
    },
    title: "--Thu TNCN--"
}
const schema2 =
{
    type: "object",
    properties: {
        Input: {
            type: "object",
            properties: {
                Loan_type: {
                    type: "string",
                    title: "Loan type",
                    enum: [
                        "Planning",
                        "Item"
                    ]
                },
                Name: {
                    type: "string",
                    title: "Tn nhn vin"
                },
                Datetime: {
                    type: "string",
                    format: "date-time",
                    description: "Thi gian ng k vay mn"
                },
                Expired_days: {
                    type: "string",
                    format: "date-time",
                    description: "Ngy ht hn"
                },
                Value: {
                    type: "number",
                    title: "S tin",
                    description: "Nhp s tin",
                    minimum: 1000000,
                    maximum: 10000000
                },
                H_s: {
                    type: "string",
                    enum: [
                        "5%",
                        "10%-1-2 triu VND",
                        "15%-3-4 triu VND",
                        "20%-5-6 triu VND",
                        "25%-7-8 triu VND",
                        "30%-9-10 triu VND",
                    ],
                    default: "0",
                    title: "Chn bc"
                },
                Refund_date: {
                    type: "string",
                    format: "date-time",
                    description: "Ngy hon tr"
                }
            },
            title: "Loans",
            required: [
                "Loan_type",
                "Name",
                "Datetime",
                "Expired_days",
                "Value",
                "H_s",
                "Refund_date"
            ]
        }
    },
    title: "--Loans--",
    required: [
        "Input",
    ]
}
const schema3 = {
    type: "object",
    properties: {
      Input: {
        type: "object",
        properties: {
          BHXH: {
            "type": "number",
            "description": "8%"
          },
          BHTN: {
            "type": "number",
            "description": "1%"
          },
          BHYT: {
            "type": "number",
            "description": "1.5%"
          },
          Lng_ng_bo_him: {
            type: "number",
            description: "Nhp lng ng bo him",
            default: 0,
            title: "Lng ng bo him"
          }
        },
        title: "Bo him",
        required: ["BHXH", "BHTN", "BHYT", "Lng_ng_bo_him"]
      },
    },
    title: "--Bo him--",
    required: ["Input"]

  }
  
const schema_output = {
    type: "object",
    properties: {
        Salary_no_tax: {
            type: "number",
            description: "Lng cha thu = Lng c bn + Sum(s tin_KPI)*Balance + Bonus",
            title: "Lng cha thu",
            readOnly: true,
            default: 0
        },
        Thu_TNCN: {
            type: "number",
            description: "Thu TNCN = (Lng cha thu - tin ng bo him - gim tr bn thn)*h s",
            readOnly: true,
            default: 0
        },
        Loan_amount: {
            type: "number",
            description: "Loan_amount = Value * h s",
            readOnly: true,
            default: 0
        },
        Thnh_tin_BH: {
            type: "number",
            description: "Thnh tin = (T_l_BHXH + T_l_BHTN + T_l_BHYT)*Lng ng bo him",
            readOnly: true,
            default: 0
          }
    },
    required: ["Salary_no_tax", "Thu_TNCN", "Loan_amount", "Thnh_tin_BH"],
    title: "Output"
};
const uiSchema = {
    // nh ngha giao din ca form  y
    "ui:options": {
        includeSubmitButton: false // n nt Submit
    }
};


const MyForm = () => {
    const [formData, setFormData] = useState({});
    const [isLoading, setIsLoading] = useState(false);
    const [salary, setSalary] = useState({});

    const [thuetncn, setThuetncn] = useState({});

    const onSubmit = async ({ formData }) => {
        setIsLoading(true);
        // Simulate API call or processing
        try {
            await fetchData({ data: formData, schema: schema });
            await OutData();
            setTimeout(() => {
                // Handle form submission here
                console.log("Form data:", formData);
                setIsLoading(false);
            }, 2000); // Simulating 2 seconds delay for API call or processing
        } catch (error) {
            console.log(error.error);
            setIsLoading(false);
        }
    };

    const fetchData = async (formData) => {
        const functionName = "datasalary";
        const args = [formData, user.id]; // Truyn d liu ng nh dng
        try {
            const res = await user.callFunction(functionName, ...args);
            console.log("success: ", res);
        } catch (error) {
            console.log("err:", error.error);
        }
    };
    const OutData = async () => {
        const functionName = 'datasalary_module';
        try {
            const res = await user.callFunction(functionName);
            setSalary(res[0]?.public?.output?.jsonData?.salary_no_tax);
            console.log('success: ', res);
        } catch (error) {
            console.log('err:', error.error);
        }
    };
//--------------------------------------------------    
    const fetchData1 = async (formData) => {
        const functionName = "datathuetncn";
        const args = [formData, user.id]; // Truyn d liu ng nh dng
        console.log("arg", args);
        try {
            const res = await user.callFunction(functionName, ...args);
            console.log("success: ", res);
        } catch (error) {
            console.log("err:", error.error);
        }
    };
    const onSubmit1 = async ({ formData }) => {
        setIsLoading(true);
        // Simulate API call or processing
        try {
            await fetchData1({ data1: formData });
            await OutData1();
            setTimeout(() => {
                // Handle form submission here
                console.log("Form data1:", formData);
                setIsLoading(false);
            }, 2000); // Simulating 2 seconds delay for API call or processing
        } catch (error) {
            console.log(error.error);
            setIsLoading(false);
        }
    };
    const OutData1 = async () => {
        const functionName = 'datathuetncn_module';
        try {
            const res = await user.callFunction(functionName);
            setThuetncn(res[0]?.public?.output?.jsonData?.thueTNCN);
            console.log('success: ', res);
        } catch (error) {
            console.log('err:', error.error);
        }
    };
//--------------------------------------------------
    const onSubmit2 = async ({ formData }) => {
        setIsLoading(true);
        // Simulate API call or processing
        try {
            await fetchData2({ data2: formData });
            await OutData2();
            setTimeout(() => {
                // Handle form submission here
                console.log("Form data2:", formData);
                setIsLoading(false);
            }, 2000); // Simulating 2 seconds delay for API call or processing
        } catch (error) {
            console.log(error.error);
            setIsLoading(false);
        }
    }
    const fetchData2 = async (formData) => {
        const functionName = "dataloan";
        const args = [formData, user.id]; // Truyn d liu ng nh dng
        console.log("arg", args);
        try {
            const res = await user.callFunction(functionName, ...args);
            console.log("success: ", res);
        } catch (error) {
            console.log("err:", error.error);
        }
    };
    const [loan, setLoan] = useState({});
    const OutData2 = async () => {
        const functionName = 'dataloan_module';
        try {
            const res = await user.callFunction(functionName);
            setLoan(res[0].public?.output?.jsonData?.loan);
            console.log('success: ', res);
        } catch (error) {
            console.log('err:', error.error);
        }
    };
//--------------------------------------------------------------
    const onSubmit3 = async ({ formData }) => {
        setIsLoading(true);
        // Simulate API call or processing
        try {
            await fetchData3({ data3: formData });
            await OutData3();
            setTimeout(() => {
                // Handle form submission here
                console.log("Form data3:", formData);
                setIsLoading(false);
            }, 2000); // Simulating 2 seconds delay for API call or processing
        } catch (error) {
            console.log(error.error);
            setIsLoading(false);
        }
    }
    const fetchData3 = async (formData) => {
        const functionName = "databaohiem";
        const args = [formData, user.id]; // Truyn d liu ng nh dng
        console.log("arg", args);
        try {
            const res = await user.callFunction(functionName, ...args);
            console.log("success: ", res);
        } catch (error) {
            console.log("err:", error.error);
        }
    }
    const [baohiem, setBaohiem] = useState({});
    const OutData3 = async () => {
        const functionName = 'databaohiem_module';
        try {
            const res = await user.callFunction(functionName);
            setBaohiem(res[0].public?.output?.jsonData?.giabaohiem);
            console.log('success: ', res);
        } catch (error) {
            console.log('err:', error.error);
        }
    }
//--------------------------------------------------------------    
    return (
        <div className="container">
            <h1 className="text-center">Form</h1>
            <Form
                schema={schema}
                formData={formData}
                onSubmit={onSubmit}
                validator={validator}
                className="custom-form"
            />
            {isLoading && <p>Loading...</p>}
            <Form
                schema={schema1}
                formData={formData}
                onSubmit={onSubmit1}
                validator={validator}
                className="custom-form"
            />
            {isLoading && <p>Loading...</p>}
            <Form
                schema={schema2}
                formData={formData}
                onSubmit={onSubmit2}
                validator={validator}
                className="custom-form"
            />
            <Form
                schema={schema3}
                formData={formData}
                onSubmit={onSubmit3}
                validator={validator}
                className="custom-form"
            />
            {isLoading && <p>Loading...</p>}
            <h2 className="text-center">Output</h2>
            <Form
                schema={schema_output}
                formData={{ Salary_no_tax: salary, Thu_TNCN: thuetncn, Loan_amount: loan, Thnh_tin_BH: baohiem }}
                validator={validator}
                uiSchema={uiSchema}
                className="custom-form"
            />

        </div>
    );
};

export default MyForm;
